.gallery {
  position: relative;
  margin-top: 80px;
  font-family: "Poppins", "Arial", sans-serif;

  ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }

  p {
    margin: 0;
  }

  &::after {
    content: "";
    position: absolute;
    bottom: 120px;
    height: 1px;
    left: 0;
    width: 100vw;
    background-color: $line;
  }

  &--no-js {
    margin-top: 24px;
    margin-bottom: 100px;
    width: 442px;

    @media (max-width: $mobile-width-only) {
      width: 280px;
    }

    &::after {
      display: none;
    }
  }

  &--js {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 2;
    margin: auto;
    display: none;
  }

  @media (max-width: $tablet-width-only) {
    margin-top: 50px;
  }

  @media (max-width: $mobile-width-only) {
    margin-top: 38px;
  }
}

.gallery__logo {
  margin-left: 100px;
}

.gallery__item {
  position: relative;

  h4 {
    margin: 0;
    font-weight: 500;
    font-size: 16px;
    line-height: 130%;
  }

  @include close-open(28px, 23px);
}

.gallery__price {
  position: absolute;
  top: 17px;

  &::after {
    content: "";
    position: absolute;
    top: -26px;
    left: 10px;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background-color: $menu-background;
  }

  &--low {
    left: 38px;

    &::before {
      content: "";
      position: absolute;
      top: -18px;
      left: 10px;
      width: 100px;
      height: 1px;
      background-color: $menu-background;
    }
  }

  &--high {
    right: 25px;
  }
}

.gallery__main-wrapper {
  display: flex;

  @media (max-width: $tablet-width-only) {
    display: block;
  }
}

.gallery__filter {
  margin-left: -5px;
  margin-right: 50px;
  width: 250px;

  @media (max-width: $tablet-width-only) {
    margin-left: 0;
  }

  li {
    margin-top: -1px;
    border: 1px solid $under-line;

    @media (max-width: $tablet-width-only) {
      border-bottom: 1px solid $under-line;
    }

    label {
      display: block;
      margin-left: 60px;
      margin-top: 12px;
      margin-bottom: 2px;

      @media (max-width: $tablet-width-only) {
        margin-left: 57px;
        margin-top: 16px;
      }
    }
  }

  h4 {
    padding-left: 25px;
    padding-top: 25px;
    padding-bottom: 21px;

    @media (max-width: $tablet-width-only) {
      padding-left: 21px;
      padding-top: 30px;
      padding-bottom: 32px;
    }
  }

  &--close {
    display: none;
  }

  @media (max-width: $tablet-width-only) {
    display: none;
    width: 100%;

    &--no-js {
      display: block;
    }
  }
}

.gallery__filter-item {
  margin-top: -11px;
  // margin-top: 2px;

  margin-bottom: 29px;

  @media (max-width: $tablet-width-only) {
    margin-top: -24px;
    margin-bottom: 35px;
  }

  label {
    position: relative;
    display: inline-flex;
    user-select: none;
    cursor: pointer;
    font-weight: 500;
    font-size: 14px;
    line-height: 130%;

    &::before {
      content: "";
      position: absolute;
      top: 2px;
      left: -27px;
      width: 16px;
      height: 16px;
      flex-shrink: 0;
      flex-grow: 0;
      border: 1px solid $menu-background;
    }
  }

  input:checked + label::before {
    background-color: $menu-background;
  }

  input:focus + label,
  label:hover,
  label:hover::before {
    opacity: 0.7;
  }

  &--ranger {
    position: relative;
    margin-top: 5px;
    margin-left: 25px;
    margin-right: 25px;
    margin-bottom: 50px;
    border-top: 1px solid $main-ground;

    @media (max-width: $tablet-width-only) {
      margin-top: 3px;
      margin-bottom: 59px;
      max-width: 260px;
    }
  }
}

.gallery__filter-submit,
.gallery__filter-reset {
  width: 250px;
  height: 68px;
  font-size: 16px;
  line-height: 130%;

  @media (max-width: $tablet-width-only) {
    margin-left: 25px;
    margin-right: 25px;
    width: calc(100% - 65px);
  }

  @media (max-width: $mobile-width-only) {
    margin-left: 20px;
    margin-right: 20px;
    width: calc(100% - 40px);
  }
}

.gallery__filter-submit {
  margin-top: 35px;
  margin-bottom: 25px;
  border: none;
  color: $white;
  background-color: $menu-background;
}

.gallery__filter-reset {
  color: $letter;
  background-color: $white;
  border: 1px solid $menu-background;
}

.gallery__show-filter {
  display: none;

  @media (max-width: $tablet-width-only) {
    position: relative;
    display: block;
    margin-bottom: 50px;
    height: 68px;
    color: $white;
    background-color: $menu-background;

    &::after {
      content: "Filter";
      position: absolute;
      height: 12px;
      width: 39px;
      top: 50%;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);
    }
  }

  @media (max-width: $mobile-width-only) {
    margin-bottom: 42px;
  }
}

.gallery__wrapper {
  margin-bottom: 40px;
}

.gallery__wrapper-width {
  width: 74%;

  @media (max-width: $tablet-width-only) {
    width: 100%;
  }
}

.gallery__slider {
  padding: 0 0 70px 0;
  margin-bottom: 80px;
  height: 1548px;
  width: 100%;

  @media (max-width: $tablet-width-only) {
    height: 1292px;
  }

  @media (max-width: $slider-width) {
    height: 1704px;
  }

  @media (max-width: $mobile-width-only) {
    height: 1484px;
  }

  .swiper-slide {
    height: calc((100% - 168px) / 4); // 345px;

    @media (max-width: $slider-width) {
      height: calc((100% - 200px) / 6); //214px;
    }
  }

  .gallery__pagination {
    font-family: "Poppins", "Arial", sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 130%;

    span {
      position: relative;
      bottom: -15px;
      padding-left: 3px;
      padding-right: 10px;

      @media (max-width: $mobile-width-only) {
        padding-left: 6px;
        padding-right: 17px;
      }
    }
  }

  .swiper-pagination-bullet {
    background-color: $white;
  }
}

.gallery__image {
  text-align: center;
  color: $letter;

  img {
    max-width: 100%;

    @media (max-width: $tablet-width-only) {
      max-height: 208px;
    }

    @media (max-width: $slider-width) {
      max-height: 136px;
    }
  }

  p {
    padding-left: 8px;
    margin-top: 19px;
    margin-bottom: 6px;

    @media (max-width: $tablet-width-only) {
      position: relative;
      left: -5px;
      margin-top: 19px;
      margin-bottom: 7px;
    }

    @media (max-width: $slider-width) {
      margin-top: 20px;
      margin-bottom: 15px;

      @media (max-width: $mobile-width-only) {
        margin-top: 13px;
        margin-bottom: 12px;
      }
    }
  }

  h4 {
    margin: 0;

    @media (max-width: $tablet-width-only) {
      font-size: 14px;
    }
  }
}

.gallery__previous,
.gallery__next {
  position: absolute;
  bottom: -4px;
  z-index: 3;
  cursor: pointer;

  &:active,
  &:hover {
    opacity: 0.7;
  }

  @media (max-width: $slider-width) {
    display: none;
  }
}

.gallery__next {
  right: 0;
}

.gallery__close {
  @include close-button(23px, -15px, -15px, $white);

  border-radius: 50%;
  background-color: $black;
}

.gallery__item--show {
  display: block;
  width: 330px;
  height: 550px;
  box-shadow: 0 4px 30px $black;
  background-color: $white;

  form {
    margin-top: 24px;
  }
}

.gallery--show {
  display: block;
  max-width: 390px;
  height: 857px;
  box-shadow: 0 4px 30px $black;
  background-color: $white;

  @media (max-width: $mobile-width-only) {
    min-width: 280px;
    height: 752px;
  }
}
