.main-navigation {
  margin-top: 115px;
  border-bottom: 1px solid $line;

  ul {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding: 9px 0 18px 0;
    margin: 0 0 0 13px;
    list-style: none;
    font-family: "Poppins", "Arial", sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;

    @media (max-width: $tablet-width-only) {
      margin-left: 0;
    }
  }

  li {
    padding: 0 33px 0 0;
  }

  li:focus,
  li:hover {
    opacity: 0.7;
  }

  li:active {
    opacity: 0.3;
  }

  @media (max-width: $tablet-width-only) {
    padding-top: 21px;
    color: $white;
    background-color: $menu-background;
    border: none;

    &--closed {
      display: none;
    }

    ul {
      display: block;
      padding: 47px 0 0 0;
      width: auto;
      font-weight: 500;
      font-size: 24px;
      line-height: 36px;

      li {
        margin-bottom: 21px;
      }

      @media (max-width: $mobile-width-only) {
        padding-top: 36px;
      }
    }

    @media (max-width: $mobile-width-only) {
      margin-top: 100px;
      padding-top: 2px;
    }
  }

  .main-navigation__pad-menu {
    display: none;

    @media (max-width: $tablet-width-only) {
      display: block;
      padding: 15px 0 50px 0;
      font-weight: 500;
      font-size: 18px;
      line-height: 130%;
      color: $menu-letter;

      li {
        margin-bottom: 21px;
      }

      & li:last-child {
        margin-top: 47px;
        color: $white;

        @media (max-width: $mobile-width-only) {
          margin-top: 33px;
        }
      }
    }

    @media (max-width: $mobile-width-only) {
      padding-top: 9px;
      padding-bottom: 25px;
    }
  }
}
